.section-hero {
    position: relative;
    min-height: 100vh;
    padding-bottom: 50rem;
    @include respond(phone) {
        min-height: 80vh;
        padding-bottom: 0rem;
    }
}

.bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80%;
    overflow: hidden;
    z-index: -1;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 2;
    }

    &__content {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
    }
}

.hero-content {
    position: relative;
    z-index: 3;
    transform: translatey(50%);
}

.section-hero.u-section-padding {
    @include respond(phone) {
        // padding: 0;
        // min-height: 80vh;
    }
}
